import { useState } from "react";
import AuthForm from "components/AuthForm";
import AuthSocialLogin from "components/AuthSocialLogin";

const Auth = () => {
  const [isLoginPage, setIsLoginPage] = useState(true);
  const toggleAuthPage = () => setIsLoginPage((prev) => !prev);

  return (
    <>
      <div className="auth-header">
        <img src={process.env.PUBLIC_URL + "/favicon.ico"} alt="" />
        <h1>{isLoginPage ? "" : ""}</h1>
      </div>

      <AuthForm isLoginPage={isLoginPage} />

      <div className="auth-toggle">
        <span>{isLoginPage ? " ?" : "?"}</span>
        <button onClick={toggleAuthPage}>
          {isLoginPage ? "" : ""}
        </button>
      </div>

      {isLoginPage && <AuthSocialLogin />}
    </>
  );
};

export default Auth;
